<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot></slot>
    </div>
    <div class="dot-group">
      <span class="dot" v-for="(item, index) in dots"
        :key="index" :class="{'active': currentPageIndex === index}"></span>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import { addClass } from "common/js/dom";

export default {
  name: "slider",
  props: {
    loop: {
      type: Boolean,
      default: true
    },
    autoPlay: {
      type: Boolean,
      default: true
    },
    interval: {
      type: Number,
      default: 4000
    }
  },
  data() {
    return {
      currentPageIndex: 0,
      dots: []
    };
  },
  mounted() {
    this.$nextTick(() => {
      this._setSliderWidth();
      this._initSlider();
      this._initDots();

      if (this.autoPlay) {
        this._play();
      }
    });
    window.addEventListener("resize", () => {
      if (!this.slider) {
        return;
      }
      this._setSliderWidth(true);
      this.slider.refresh();
    });
  },
  activated() {
    if (this.autoPlay) {
      this._play();
    }
  },
  deactivated() {
    clearTimeout(this.timer);
  },
  beforeDestroy() {
    clearTimeout(this.timer);
  },
  methods: {
    _setSliderWidth(isResize) {
      this.children = this.$refs.sliderGroup.children;

      let width = 0;
      let sliderWidth = this.$refs.slider.clientWidth;
      for (let i = 0; i < this.children.length; i++) {
        let child = this.children[i];
        addClass(child, "slider-item");

        child.style.width = sliderWidth + "px";
        width += sliderWidth;
      }
      if (this.loop && !isResize) {
        width += 2 * sliderWidth;
      }
      this.$refs.sliderGroup.style.width = width + "px";
    },
    _initSlider() {
      // 初始化配置
      this.slider = new BScroll(this.$refs.slider, {
        scrollX: true,
        scrollY: false,
        momentum: false,
        snap: {
          loop: this.loop, // 循环
          threshold: 0.3,
          speed: 400 // 轮播间隔
        }
      });
      this.slider.on("scrollEnd", () => {
        let pageIndex = this.slider.getCurrentPage().pageX; // 目前index
        this.currentPageIndex = pageIndex;
        if (this.autoPlay) {
          clearTimeout(this.timer);
          this._play();
        }
      });
      this.slider.on("beforeScrollStart", () => {
        if (this.autoPlay) {
          clearTimeout(this.timer);
        }
      });
    },
    _play() {
      let pageIndex = this.currentPageIndex + 1; //下一张幻灯片index
      if (pageIndex === this.children.length) {
        pageIndex = 0;
      }
    //   console.log(pageIndex);
      this.timer = setTimeout(() => {
        this.slider.next();
      }, this.interval);
    },
      _initDots() {
    this.dots = new Array(this.children.length);
  }
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/styles/variable';

.slider
  min-height: 1px;
  position: relative;
  .slider-group
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    .slider-item
      float: left;
      box-sizing: border-box;
      overflow: hidden;
      text-align: center;
      img
        display: block;
        width: 100%;

  .dot-group
    position: absolute;
    right: 0;
    left: 0;
    bottom: 12px;
    text-align: center;
    .dot
      display: inline-block;
      margin: 0 4px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: $color-text-l;
      &.active
        border-radius: 5px;
        background: $color-sub-theme;
</style>


